* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

#datetime-picker,
button {
  padding: 5px;
  border-radius: 3px;
  border: none;
  border: 1px solid #000000;
}

.timer {
  display: flex;
  margin: 20px;
}
.field {
  margin-right: 20px;
}
.value {
  display: block;
  text-align: center;
  font-size: 36px;
}
.value-seconds {
  color: red;
}
.label {
  display: block;
  font-weight: 500;
}
